<template>
  <!--首页-->
  <div class="home">
    <!--顶部-->
    <div class="header">
      <mt-header fixed title="网站名称">
        <router-link to="/" slot="left">
          <mt-button class="icon-thumb_up"></mt-button>
        </router-link>

        <router-link to="/" slot="right">
          <mt-button class="icon-thumb_up"></mt-button>
        </router-link>
      </mt-header>
    </div>

    <!--搜索框-->
    <div class="search">
      <mt-search v-model="searchValue" cancel-text="取消" placeholder="搜索">
        <mt-cell v-for="(item, index) in searchResult" :key="index" :title="item.title" :value="item.value">
        </mt-cell>
      </mt-search>
    </div>

    <!--轮播图-->
    <div class="swiper">
      <mt-swipe :auto="3000" @change="handleChange">
        <mt-swipe-item v-for="(item, index) in bannerList" :key="index">
          <router-link to="/">
            <img :src="'/static/images/banners/' + item.picUrl" :alt="item.alt" />
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>

    <!--推荐商品-->
    <div class="best">

    </div>
  </div>
</template>

<script>
  import { Header, Search, Swipe, SwipeItem, MessageBox } from 'mint-ui';
  import axios from 'axios'

  export default {
    name: 'Home',
    data () {
      return {
        searchValue: '',
        searchResult: [
          {
            title: 'title1',
            value: 'value1'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          },{
            title: 'title2',
            value: 'value2'
          }
        ],
        bannerList: []
      }
    },
    mounted() {
      let self = this;

      axios.get('/mock/5a2967fc70188c51fc72ed39/shop/banner/list').then(res=>{
        if (res.status === 200) {
          self.bannerList = res.data.data;
        } else {
          MessageBox({ title: '警告', message: res.statusText });
        }
      });
    },
    methods: {
      handleChange(index) {
        console.log(index);
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '~@/assets/scss/const.scss';
  @import '~@/assets/scss/mixin.scss';

  .home {

    .header {
      .mint-header {
        z-index: 9;
      }
    }

    .search {
      top: 40px;
      position: relative;
      z-index: 7;

      .mint-search {
        height: 280px;
      }
    }

    .swiper {
      top: -196px;
      height: 150px;
      position: relative;

      img {
        width: 100%;
        height: 150px;
      }
    }


  }

</style>



